import React ,{Component} from 'react';
import './add.css';
import {NavLink} from 'react-router-dom'
class Add extends Component{
        state = {
            items:{id:Math.random(),title:'',content:'',draft:false,pinned:false,date:'',section:''}
        }
        handleTitle = (e)=>{
            let items = this.state.items;
            items.title = e.target.value;
            this.setState({
                items:items
            })
        }
        handleContent = (c) =>{
            let items = this.state.items;
            items.content = c.target.value;
            this.setState({
                items:items
            })
        }
        pinned = (e)=>{
            let x = e.target;
            x.classList.toggle('active');
            let items = this.state.items;
            items.pinned = x.classList.contains('active');
            this.setState({
                items:items
            })
        }
        sectionChange = (e)=>{
            let x = e.target.value;
            let items = this.state.items;
            items.section = x;
            this.setState({
                items:items
            })
        }
        addnewitem = ()=>{
            let  convertedStartDate = new Date(),
             month = convertedStartDate.getMonth() + 1,
             day = convertedStartDate.getDate(),
             year = convertedStartDate.getFullYear(),
             shortStartDate = month + "/" + day + "/" + year;
            
            let items = this.state.items;
            items.date = shortStartDate;
            this.setState({
                items:items
            })
            if(!(this.state.items.content === '')){
                this.props.data(this.state.items)
                this.setState({
                items:{id:Math.random(),title:'',content:'',draft:false,pinned:'',data:'',section:''}
            })
            }else{
                this.setState({
                    items:{id:Math.random(),title:'',content:'',draft:false,pinned:'',data:'',section:''}
                })
            }
        }
        back = ()=>{
            this.setState({
                items:{id:Math.random(),title:'',content:'',draft:false,pinned:'',data:'',section:''}
            })
        }
        render(){
            return(
                <form className='add-form'>
                    <div className='add-form-content'>
                        <input type='text' className='title-field'placeholder='type the Title' onChange={this.handleTitle} title='title'/>
                        <textarea type='text' className='content-field' placeholder='type the Content' onChange={this.handleContent} title='content'/>
                    </div>
                    
                    <div className='add-controls'>
                        <NavLink to='/notes'  className='icon-header' title='back' onClick={this.back}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z"/></svg>  
                        </NavLink>
                        
                        <div className='right-side'>
                        <div className='more-controls'>
                            <div className='pinned'>

                                <label onClick={this.pinned}>
                                    <svg  xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M11 17h2v5l-2 2v-7zm3.571-12c0-2.903 2.36-3.089 2.429-5h-10c.068 1.911 2.429 2.097 2.429 5 0 3.771-3.429 3.291-3.429 10h12c0-6.709-3.429-6.229-3.429-10z" title='pinned' /></svg>
                                </label>

                            </div>
                            <div>
                                <select onChange={this.sectionChange}>
                                    <option value='All Tasks'>All Tasks</option>
                                    <option value='Sport'>Sport</option>
                                    <option value='Study'>Study</option>
                                    <option value='school'>school</option>
                                </select>   
                            </div>
                        </div>
                        <NavLink to='/notes' onClick={this.addnewitem} className='icon-header' title='add'>
                            
                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M20.285 2l-11.285 11.567-5.286-5.011-3.714 3.716 9 8.728 15-15.285z"/></svg> 
                            </NavLink>
                        </div>
                    </div>

                </form>
            )
        }
        
}
export default Add