import React,{Component} from 'react';
import './App.css';
import './component/navigator/nav.css';
import {BrowserRouter,Route, Switch} from 'react-router-dom';
import Nav from './component/navigator/nav';
import Notes from './component/notes/notes'
import Tasks from './component/tasks/tasks'
import Drafts from './component/drafts/drafts'
import Settings from './component/settings/settings'
import Add from './component/add/add'
import NotFound from './component/not-found/not-found'
import Edit from './component/edit/edit'
class App extends Component{
  state = {
    items:[]
  }
  AddItems = (i)=>{

    let items = this.state.items;
    items.push(i);
    this.setState({
      items:items
    })
    }
  Senddata = (i)=>{
    console.log('asdasd')
    return i 
  }
  Getdata = ()=>{
    let x = this.Senddata();
    console.log(x)
  }
  render(){
    return(
      <BrowserRouter>
        <Nav/>
        <Switch>
        <Route path='/' exact component={props => <Notes info={this.state.items} Senddata = {this.Senddata}/>}/>
        <Route path='/abdo' exact component={props => <Notes info={this.state.items} Senddata = {this.Senddata}/>}/>
        <Route path='/notes'  component={props => <Notes info={this.state.items} Senddata = {this.Senddata}/>}/>
        <Route path='/tasks'  component={Tasks}/>
        <Route path='/drafts'  component={Drafts}/>
        <Route path='/settings'  component={Settings}/>
        <Route path='/add'   component={props => <Add data={this.AddItems}/>} />
        <Route path='/edit' component={props => <Edit  Getdata = {this.Getdata}/>}/>
        <Route path="/:dd" component={NotFound}/>
        
        </Switch>
      </BrowserRouter>
    )
  }
}
export  default App;
