import React from 'react';
import './notes.css';
import Header from './../header/header.js';
import { NavLink } from 'react-router-dom';
const Notes  = (props)=>{
    const{info,Senddata} = props;
    const infoUndraft = info.filter(i=> i.draft === false), 
    infoFliterdPinned = infoUndraft.filter(i=> i.pinned === true),
    infoFliterdOther = infoUndraft.filter(i => i.pinned === false),
    allTasksPinned = infoFliterdPinned.map( e =>{
        return(
            <NavLink to='/notes'  key= {e.id} className='task' onClick={()=>{Senddata(e)}}>
                <div>
                    <h3>{e.title} </h3>
                    <p className='task-content'>{e.content}</p>
                </div>
                <p className='date'>{e.date}</p>
            </NavLink>
        )
    }),
    allTasksOther = infoFliterdOther.map( e =>{
        return(
            <NavLink to='/edit'  key= {e.id} className='task'>
                <div>
                    <h3>{e.title} </h3>
                    <p className='task-content'>{e.content}</p>
                </div>
                <p className='date'>{e.date}</p>
            </NavLink>
        )
    })
    return(
        <div className='container'>
            <Header/>
                <div className='header'>
                    <h1>All Tasks</h1>
                    <span>
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M11 11h-11v-11h11v11zm13 0h-11v-11h11v11zm-13 13h-11v-11h11v11zm13 0h-11v-11h11v11z"/></svg>
                    </span>
                </div>
                <div className='pinned'>
                    <h4 className='title-content'>pinned</h4>
                    <div className='content'>
                        {allTasksPinned}
                    </div>
                </div>
                <div className='other'>
                    <h4 className='title-content'>others</h4>
                    <div className='content'>
                        {allTasksOther}
                    </div>
                </div>
        </div>
    )
}
export default Notes;